.modals {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-image: linear-gradient(315deg, #734ae8 0%, #89d4cf 74%);
  padding: 6rem;
  border-radius: 5px;
  box-shadow: 0 3rem 5rem rgba(0, 0, 0, 0.3);
  z-index: 12;
  width: 450px;
}

.close-modal {
  position: absolute;
  top: 0.5rem;
  right: 2rem;
  font-size: 4rem;
  cursor: pointer;
  color: #fff;
  background: none;
  border: none;
  padding: 0;
  transition: all 0.2s linear;
}

.close-modal:hover {
  transform: rotate(90deg) scale(1.2);
  color: #226500;
}

.hidden {
  display: none;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  backdrop-filter: blur(3px);
  z-index: 11;
}

.modal-form label {
  width: 100%;
}

.modal-form button,
.modal-form input,
.modal-form select {
  width: 100%;
  height: 61px;
  border: none;
  margin: 1rem 0;
  border-radius: 5px;
  outline: none;
  caret-color: #226500;
}

.modal-form select {
  cursor: pointer;
  color: #226500;
  padding-left: 1.6rem;
}

.modal-form select option {
  color: #226500;
}

.modal-form input::placeholder {
  color: #226500;
  font-size: 19px;
}

.modal-form input {
  padding-left: 1.6rem;
  color: #226500;
  font-size: 19px;
}

.modal-form button {
  background-color: #226500;
  color: #fff;
  font-size: 21px;
  font-family: "Poppins", sans-serif;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.1s linear;
}

.modal-form button:hover {
  background-color: #2b7c02;
  transform: translateY(-4px);
}

.modal-form button:active {
  transform: translateY(-2px);
  background-color: #226500;
}

@media (max-width: 992px) {
  .modals {
    width: 60%;
  }
}

@media (max-width: 767px) {
  .modals {
    width: 70%;
  }
  .modal-form button,
  .modal-form input,
  .modal-form select {
    height: 40px;
  }
  .modal-form input::placeholder {
    font-size: 12px;
  }
  .modal-form input,
  .modal-form select {
    padding-left: 1rem;
    font-size: 12px;
  }
  .modal-form button {
    font-size: 18px;
  }
}

@media (max-width: 420px) {
  .modals {
    width: 80%;
  }
}
